import React, { Component } from 'react';
import PropTypes from 'prop-types';
import AntMenu from 'antd/lib/menu';
import 'antd/lib/menu/style/index.css';
import StringToHTML from '../StringToHTML';
import styled from 'styled-components';
import mixins from '../styles/mixins';
import theme from '../styles/theme';

const MtMenu = styled.div`
  .ant-anchor-wrapper {
    overflow: hidden;
  }
  .ant-select-dropdown-menu-item {
    background-color: ${theme.colors.INDIGO};
    color: #fff;
  }
  .ant-select-dropdown {
    background-color: ${theme.colors.INDIGO};
    color: #fff;
  }
  .ant-select-dropdown-menu {
    background-color: #fff;
    color: ${theme.colors.GREY};
    padding: 0px 5px;
    margin: 8px;
    border-radius: 4px;
    height: 32px;
    line-height: 32px;
  }
  .ant-dropdown-menu-submenu-title {
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
    &:hover {
      background-color: ${theme.colors.INDIGO};
      color: #fff;
    }
  }
  .ant-dropdown-menu-item {
    background-color: #fff;
    color: ${theme.colors.OUTER_SPACE};
    padding: 0px 15px;
    margin: 4px 8px;
    border-radius: 4px;
    height: 32px;
    line-height: 32px;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
    &:hover {
      background-color: ${theme.colors.INDIGO};
      color: #fff;
    }
  }
  .ant-menu-horizontal {
    & > .ant-menu-item {
      &:hover {
        color: ${theme.colors.CONGRESS_BLUE};
      }
    }
    & > .ant-menu-submenu {
      &:hover {
        color: ${theme.colors.CONGRESS_BLUE};
      }
    }
    & > .ant-menu-item-active,
    & > .ant-menu-submenu-active,
    & > .ant-menu-item-selected,
    & > .ant-menu-submenu-selected {
      color: ${theme.colors.CONGRESS_BLUE};
    }
  }
  .ant-menu-vertical {
    & > .ant-menu-item {
      color: #8e9194;
      padding: 0px;
      margin: 0px;
      line-height: 20px;
      height: 20px;

      text-align: left;
      &:hover {
        color: black;
      }
    }
  }
  .ant-menu-item {
    font-size: 15px;
    color: #8e9194;
    padding: 0px 8px;
    &:hover {
      color: #fff;
    }
    b {
      font-weight: normal;
    }
  }
  .ant-dropdown-trigger {
    font-size: 13px;
  }

  & {
    .ant-menu-inline > .ant-menu-submenu > .ant-menu-submenu-title {
      text-align: left;
      padding-left: 0px !important;
      font-size: 11px;
      text-transform: uppercase;
      height: auto;
      line-height: 16px;
      margin: 0px 0px 8px 0px;
      color: ${theme.colors.OUTER_SPACE};

      &:hover {
        color: ${theme.colors.OUTER_SPACE};
      }

      .ant-menu-submenu-arrow {
        display: none;
      }
    }
  }
  & {
    .ant-menu-sub.ant-menu-inline > .ant-menu-item {
      padding-left: 0px !important;
      float: left;
      text-align: left;
      height: auto;
      line-height: 20px;
      margin: 0px;
      font-size: 12px;
    }
  }

  .ant-anchor-link {
    padding: 6px 0 6px 0px;
    line-height: 1;
    display: list-item;
    margin-left: 17px;
    list-style-type: disc;
    color: ${theme.colors.INDIGO};

    a {
      color: ${theme.colors.INDIGO};
      font-size: 12px;
      ${mixins.truncate('250px')};
    }
  }
  .ant-menu-inline,
  .ant-menu-light {
    border-right: 0px;
  }
  .cautious {
    color: ${theme.colors.BITTERSWEET};
    &:hover {
      background-color: ${theme.colors.BITTERSWEET};
      color: #fff;
    }
  }
`;

class Menu extends Component {
  static propTypes = {
    options: PropTypes.array,
    onClick: PropTypes.func,
    mode: PropTypes.string,
    children: PropTypes.node,
    prefixCls: PropTypes.string,
    itemStyle: PropTypes.object
  };

  onClick = ({ key }) => {
    if (this.props.onClick) {
      this.props.onClick(key);
    }
  };
  static defaultProps = {
    style: { paddingLeft: '0px' }
  };

  render() {
    let { options, children, itemStyle } = this.props;
    return (
      <MtMenu>
        <AntMenu {...this.props} onClick={this.onClick}>
          {!options && children}
          {options &&
            options.map((option, index) => {
              return (
                <AntMenu.Item
                  key={option.key || index}
                  style={itemStyle}
                  className={option.cautious ? 'cautious' : ''}
                >
                  {typeof option.content === 'string' ? (
                    <StringToHTML content={option.content} />
                  ) : (
                    option.content
                  )}
                </AntMenu.Item>
              );
            })}
        </AntMenu>
      </MtMenu>
    );
  }
}

Menu.ItemGroup = AntMenu.ItemGroup;
Menu.SubMenu = AntMenu.SubMenu;
Menu.Item = AntMenu.Item;
export default Menu;
